import React, { Component } from 'react';
import {
  Input,
  Form,
  message,
} from 'antd';
import { getStudyPeriodTypes } from 'services/planSubjectDetail​';
import { getStudySessions } from 'services/studySession';
import { getStaffs } from 'services/staff';
import { getBuildingRoomForSelect } from 'services/buildingRoom';

import TableForm from './tableForm';
import SelectTable from './SelectTable';

import './style.scss';

@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {
      studyPeriodTypesData: [],
      studySessionsData: [],
      staffsData: [],
      buildingRoomForSelectData: [],
    }
  }

  componentWillMount() {
    Promise.all([
      getStudyPeriodTypes({}),
      getStudySessions({}),
      getStaffs({}),
      getBuildingRoomForSelect({}),
    ])
      .then((responses) => {
        this.setState({
          studyPeriodTypesData: [...responses[0].data.items],
          studySessionsData: [...responses[1].data.items],
          staffsData: [...responses[2].data.items],
          buildingRoomForSelectData: [...responses[3].data.items],
        });
      })
      .catch(() => {
        message.error('Có lỗi xãy ra');
      });
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  columns = [
    {
      title: 'Tên lớp',
      dataIndex: 'className',
    },
    {
      title: 'Tên môn',
      dataIndex: 'subjectLabel',
    },
    {
      title: 'Số học trình',
      dataIndex: 'noOfCredit',
    },
    {
      title: 'Lớp ghép',
      dataIndex: 'classSubjectConcat',
    },
    {
      title: 'Số SV',
      dataIndex: 'noOfStudent',
    },
    {
      title: 'Loại giờ',
      dataIndex: 'studyPeriodTypeId',
      render: (_text, record) => {
        const {
          getFieldDecorator,
        } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`studyPeriodTypeId-${record.autoKey}`, {
              initialValue: record.studyPeriodTypeId,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Loại giờ!',
                },
              ],
            })(
              <SelectTable
                keyValue="value"
                keyLabel="label"
                data={this.state.studyPeriodTypesData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'S tiết CTĐT',
      dataIndex: 'noOfPeriodProgram',
    },
    {
      title: 'Chia tiết',
      dataIndex: 'splitPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`splitPeriod-${record.autoKey}`, {
              initialValue: record.splitPeriod,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng điền Chia tiết!',
                  pattern: new RegExp("^\\d\\-\\d$|^\\d$"),
                },
              ],
            })(
              <Input style={{ width: '75px' }} />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Ca học',
      dataIndex: 'studySessionId',
      render: (_text, record) => {
        const {
          getFieldDecorator,
        } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`studySessionId-${record.autoKey}`, {
              initialValue: record.studySessionId,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Ca học!',
                },
              ],
            })(
              <SelectTable
                data={this.state.studySessionsData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Giảng viên',
      dataIndex: 'teacherId',
      render: (_text, record) => {
        const {
          getFieldDecorator,
        } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`teacherId-${record.autoKey}`, {
              initialValue: record.teacherId,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Giảng viên!',
                },
              ],
            })(
              <SelectTable
                keyValue="staffId"
                keyLabel="fullname"
                data={this.state.staffsData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Phòng học',
      dataIndex: 'roomId',
      render: (_text, record) => {
        const {
          getFieldDecorator,
        } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`roomId-${record.autoKey}`, {
              initialValue: record.roomId,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng chọn Phòng học!',
                },
              ],
            })(
              <SelectTable
                keyValue="value"
                keyLabel="label"
                data={this.state.buildingRoomForSelectData}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Từ tuần',
      dataIndex: 'fromWeek',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`fromWeek-${record.autoKey}`, {
              initialValue: record.fromWeek,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng điền Tuần bắt đầu!',
                },
              ],
            })(
              <Input style={{ width: '75px' }} />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Đến tuần',
      dataIndex: 'toWeek',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`toWeek-${record.autoKey}`, {
              initialValue: record.toWeek,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng điền Tuần kết thúc!',
                },
              ],
            })(
              <Input style={{ width: '75px' }} />
            )}
          </Form.Item>
        );
      },
    },
  ];
  render() {
    const { reload, rowKey, data, handleSelectedRows, form } = this.props;
    return (
      <TableForm
        reload={reload}
        data={data}
        handleSelectedRows={handleSelectedRows}
        rowKey={rowKey}
        columns={this.columns}
        form={form}
        ref={this.refTable}
      />
    );
  }
}

export default TrainingPlanTableDetail;



